@import "variables";
@import "mixins";
html,
body {
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: $Roboto;
    background: $lgrey;
    
    display: flex;
    flex-direction: column;
}
.main {
    flex: 1 0 auto;
}
body * {
    box-sizing: border-box;
}
.wrapper {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 0 10px; 
}
.flex {
    display: flex;
}
.text {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;
}
.scroll {
    &-pane {
        width: 100%;
        max-height: 230px;
        overflow: auto;
        outline: inherit;
    }
}
        .map {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            width: 100%;
            height: 100%;
        }
.index {
    background: url(../images/temp/20.png) no-repeat top center;
    background-size: cover;
    color: #fff;
    &__main {
        padding-top: 80px;
        height: 100%;
        
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .header {
        background: none;
        box-shadow: none;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
    }
    .logo {
        margin-bottom: 0;
    }
    .nav {
        &-a {
            margin-bottom: 0;
            border-bottom: none;
            color: #fff;
            transition: $tr;
            &:hover {
                color: $red;
            }
        }
        &__menu {
            color: #fff;
        }
        
    }
    .tel {
        color: #fff;
        &-a {
            color: #fff;
        }
    }
    &__banner {
//        height: 100%;
        @media (max-width:600px) {
            text-align: center;
            .flex {
                justify-content: center;
                flex-wrap: wrap;
            }
        }
        &-h1 {
            font-style: normal;
            font-weight: normal;
            font-size: 45px;
            line-height: 53px;
            letter-spacing: 0.06em;
            @media (max-width:600px) {
                font-size: 20px;
                line-height: 23px;
            }
        }
        &-p {
            font-style: normal;
            font-weight: 200;
            font-size: 28px;
            line-height: 33px;
            letter-spacing: 0.06em;
            
            margin-bottom: 65px;
            @media (max-width:600px) {
            font-size: 16px;
            line-height: 19px;
                margin-bottom: 55px;
            }
        }
        .btn {
            margin-right: 20px;
            min-width: 197px;
            @media (max-width:600px) {
                margin-left: 30px;
                margin-right: 30px;
                margin-bottom: 15px;
            }
        }
    }
}
