.where {
    &__banner {
        padding-top: 105px;
        padding-bottom: 100px;
        @media (min-width:769px) {
            padding-top: 150px;
            padding-bottom: 245px;
        }
        @media (max-width:600px) {
            padding-top: 38px;
            padding-bottom: 38px;
            margin-left: -10px;
            margin-right: -10px;
            padding-left: 10px;
            padding-right: 10px;
        }
        color: #fff;
        
        display: flex;
        justify-content: center;
        align-items: center;
        
        flex-direction: column;
        
        background: url(../images/temp/18.jpg) no-repeat top center;
        background-size: cover;
        
//        position: relative;
//        z-index: 2;
        .catalog-left-button {
            display: flex;
            border-color: #fff;
            &.active {
                .icon-arrUp {
                    transform: rotate(-90deg);
                }
            }
        }
        &-h1 {
            margin-bottom: 39px;
        }
        &-d {
            max-width: 400px;
            width: 100%;
            position: relative;
            &-span {
                margin-right: 15px;
            }
            &d { 
                position: absolute;
                top: calc( 100% + 22px );
                padding: 22px 20px; 
                
                left: 0;
                right: 0;
                
                z-index: 10;
                
                text-align: center;

                display: none;
                &-head {
                    padding-bottom: 20px;
                    border-bottom: 1px solid $grey;
                    
                    display: flex;
                    justify-content: space-between;
                    .btn {
                        width: 48%;
                    }
                }
                &-block {
                    justify-content: space-between;
                }
                &-item {
                    width: 100%;
                }
                &-a {
                    display: block;
                    margin-top: 20px;
                    text-align: center;
                    
                    color: $grey;
                    
                    text-decoration: none;
                    &.active,
                    &:hover {
                        color: $red;
                    }
                    
                }
            }
        }
        .btn {
            &.active {
                background: #752023;
                border-color: #752023;
                color: #fff;
            }
        }
        &-toggle {
            display: none;
            &.active {
                display: block;
            }
        }
    }
    &__map {
        position: relative;
        //z-index: 3;
        
        min-height: 390px;
        max-width: 996px;
        width: 100%;
        
        margin-left: auto;
        margin-right: auto;
        
        margin-top: -195px;
        margin-bottom: -195px;
        
        @media (max-width:768px) {
            display: none;
        }
    }
    &__adress {
        @media (min-width:769px) {
            padding-top: 260px; 
        }
        
        margin-bottom: 95px;
        @media (max-width:600px) {
            background: none!important;
            box-shadow: none!important;
            padding: 15px 0;
            .btn {
                background: none!important;
            }
        }
        &-icon {
            
        }
        .btn {
            padding-left: 45px;
            padding-right: 45px;
            margin-bottom: 20px;
        }
        &-item {
            justify-content: space-between;
            align-items: flex-start;
            
            color: $blue;
            
            border-bottom: 1px solid  rgba( $grey, .3 );
            margin-bottom: 20px;
            @media (min-width:601px) {
                margin-bottom: 36px;
            }
            
            max-width: 996px;
            width: 100%;

            margin-left: auto;
            margin-right: auto;
            
            @media (max-width:600px) {
                flex-direction: column;
                align-items: center;
            }
        }
        &-name {
            padding-top: 14px;
            font-style: normal;
            font-weight: normal;
            font-size: 20px;
            line-height: 23px;
            padding-bottom: 20px;
            @media (min-width:601px) {
                padding-bottom: 37px;
            }
        }
        &-info {
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 19px;
            
            margin-bottom: 20px;
        }
    }
}