.header {
    padding: 15px 0 30px 0;
    background: #FFFFFF;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.05);
    
    position: relative;
    z-index: 20;
    .flex {
        align-items: center;
        justify-content: space-between;
    }
}

.nav {
    @media (max-width:769px) {
        position: absolute;
        top: 0;
        left: -110%;
        right: 110%;
        bottom: 0;
        background: #fff;
        &.open {
            left: 0;
            right: 0;
        }
    }
    &-a {
        font-family: $Roboto;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 16px;
        
        color: $grey;
        padding: 8px 0;
        margin: 0 15px;
        border-bottom: 1px solid #fff;
        
        display: inline-block;
        text-decoration: none;
        
        margin-bottom: 15px;
        &:hover,
        &.active {
            border-color: $blue;
        }
    }
    &__menu {
        color: $blue;
        text-decoration: none;
        font-size: 30px;
        display: none;
        color: $red;
        
        cursor: pointer;
        @media (max-width:769px) {
            display: block;
        }
    }
    &__mob {
        position: fixed;
        
        box-shadow: 0px 4px 6px rgba(0,0,0,0.05);
        
        top: 0;
        bottom: 0;
        background: #fff;
        
        padding: 60px 35px;
        width: 320px;
        
        
        left: -330px;
        
        z-index: 10;
        
        transition: left ease 0.4s;
        &:after {
            
                width: 0;
                right: 0;
                left: 100%;
                content: '';
                display: block;
                position: fixed;
                top: 0;
                
                bottom: 0;
                
                background: rgba($blue,.4);
                opacity: 1;
                transition: all 0.4s ease;
            }
        &.open {
            left: 0;  
            &:after {
                left: 320px;
                width: 100%;
            }
        }
        .nav {
            &_a {
                display: block;
                text-decoration: none;
                color: $grey;
                &:hover {
                    color: $red;
                }
                + .nav_a {
                    margin-top: 23px;
                }
            }
        }
        .hr {
            display: block;
            margin: 25px 0;
            height: 1px;
            border: none;
            background: rgba($grey,.5);
        }
        &-close {
            position: absolute;
            top: 18px;
            left: 28px;
            
            font-size: 13px;
            text-decoration: none;
            color: $grey;
            &:hover {
                color: $red;
            }
        }
        .tel {
            text-align: center;
        }
    }
}
.tel {
    font-size: 18px;
    font-weight: bold;
    color: $grey;
    
    text-align: right;
    &-a {
        text-decoration: none;
        color: $grey;
        
        display: block;
        
        transition: $tr;
        &:hover {
            color: $red;
        }
    }
    &-span {
        font-weight: 100;
    }
}
.header {
    &-top {
        align-items: flex-end;
        @media (max-width:769px) {
            justify-content: space-between;
            .flex {
                width: 100%;
            }
            .tel {
                display: none;
            }
        }
    }
    &-bottom {
        padding-top: 25px;
        @media (max-width:650px) {
            flex-wrap: wrap;
        }
    }
    @media (max-width:650px) {
        &-button {
            justify-content: space-between;
            display: flex;
            width: 100%;
            .btn {
                width: 48%;
            }
        }
    }
}
.logo {
    margin-bottom: 9px;
    @media (max-width:769px) {
        width: calc( 50% + 80px );
    }
    &-img {
        width: 100%;
        max-width: 160px;
    }
}
