.catalog {
    &-top {
        padding: 5px 10px 30px 10px;
        justify-content: space-between;
        align-items: center;
        @media (max-width:915px) {
            flex-wrap: wrap;
            .block-head {
                width: 100%;
                font-size: 20px;
                margin: 30px 0;
            }
            
            .jq-selectbox {
                vertical-align: middle;
                cursor: pointer;
                width: 48%;
            }
   
        }
    }
    &-left {
        width: 265px;
        min-width: 265px;
        margin-right: 23px;
        @media (max-width:915px) {
            position: absolute;
            left: -300px;
            &.open {
                z-index: 12;
                left: 10px;
                margin-top: -20px;
            }
            
        }
        
        &-button {
            @media (min-width:915px) {
                display: none;
            }
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-radius: 35px;
            padding: 11px 13px;
            
            border: 1px solid $grey;
            
            
            
            cursor: pointer;
            .icon {
                &-arrUp {
                    transition: $tr;
                    display: inline-block;
                    transform: rotate(90deg);
                }
            }
            
            
        }
        &-block {
            width: 48%;
            &.open {
                &:after {
                    content: '';
                    display: block;
                    position: fixed;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    background: rgba(0,0,0,0.5);
                    z-index: 11;
                }
                .catalog-left-button {
                    background: #fff;
                    position: relative;
                    z-index: 12;
                }
            }
        }
    }
    &-right {
        
    }
    &__filter {
        position: relative;
        &-block {
            
        }
        &-body {
            
        }
        &-toggle {
            padding-top: 37px;
        }
        &-head {
   
            font-style: normal;
            font-weight: bold;
            font-size: 16px;
            line-height: 19px;
            
            //margin-bottom: 27px;
            
            display: flex;
            justify-content: space-between;
            align-items: center;
            
            cursor: pointer;
            transition: $tr;
            [class^="icon-"] {
                display: inline-block; 
                transform: rotate(-90deg);
                transition: $tr;
            }
            &.close {
                //margin-bottom: 0;
                [class^="icon-"] {
                    transform: rotate(90deg);
                }
            }
            
        }
        &-reset {
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 16px;
            
            text-align: center;
            display: block;
            
            padding: 10px;
            color: $blue;
            text-decoration: none;
        }
        &-accept {
                position: absolute;
                top: 0;
                left: 92%;
                z-index: 10;
                background: url(../images/accept.svg) no-repeat top center;
                color: #fff;
                text-decoration: none;
                padding: 20px 21px 21px 30px;
                border-radius: 5px;
            display: none;
            @media (max-width:500px) {
                    position: relative;
                    top: auto!important;
                    left: auto!important;
                    z-index: 10;
                    background: $red;
                    text-align: center;
                    color: #fff;
                    text-decoration: none;
                    padding: 20px 21px 21px 30px;
                    border-radius: 5px;
                    display: block!important;
            }
        }
    }
    
    &__list {
        flex-wrap: wrap;
        margin-bottom: 58px;
    }
    &__item {
        border: 1px solid #EDEEF2;
        width: calc( 25% - 8px );
        margin-left: 4px;
        margin-right: 4px;
        
        padding: 30px 23px;
        
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 16px;
        text-align: center;
        @media (max-width:700px) {
            width: calc( 50% - 8px );
            padding: 17px 10px;
        }
        &:hover {
            .catalog__item-image {
                transform: scale(1.1);
            }
        }
        &-img {
            max-height: 173px;
            overflow: hidden;
            
            display: flex;
            justify-content: center;
            align-items: center;
        }
        &-image {
            max-width: 100%;
            display: block;
            transition: $tr;
        }
        &-name {
            
            color: $blue;
            text-decoration: none;
            
            display: block;
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            height: 30px;
            margin-bottom: 26px;
        }
        &-btn {
            padding: 8px 0;
            width: 100%;
            max-width: 136px;
            &:hover {
                border-color: $red;
                background: $red;
                color: #fff;
            }
        }
    }
    &__more {
        text-align: center;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
        margin-bottom: 70px;
        &-text {
            margin-bottom: 23px;
            
            font-style: normal;
            font-weight: 300;
            font-size: 12px;
            line-height: 16px;
        }
    }
    &__item {
        &-single {
            
        }
        &-info {
//            @media (max-width:768px) {
//                display: none;
//            }
            
        }
        &-name {
//            font-style: normal;
//            font-weight: bold;
//            font-size: 24px;
//            line-height: 28px;
//            letter-spacing: 0.01em;
        }
        &-img {
            &__carousel {
                
            }
        }
        &-block {
            width: 50%;
            text-align: left;
            &:not(.catalog__item-info) {
                padding: 0 25px 0 0
            } 
            @media (max-width:768px) {
                width: 100%;
            }
        }
    }
    &__single {
        width: 100%;
        margin: 0 0 75px 0;
        @media (max-width:768px) {
            background: none;
            flex-direction: column;
            box-shadow: none;
            margin: 0;
            padding: 30px 0;
            &:before {
                content: attr(data-block);
                font-style: normal;
                font-weight: bold;
                font-size: 20px;
                line-height: 23px;
                letter-spacing: 0.01em;
                padding-bottom: 44px;
                padding-top: 20px;
            }
            
        }
        
        .owl {
            &-item {
                display: flex;
                justify-content: center;
                align-items: center;
            }
            &-next,
            &-prev {
                outline: none;
                font-size: 0!important;
                display: flex;
                
                position: absolute;
                top: 50%;
                transform: translate(0,-50%);
                
                &:before {
                    content: "\e901";
                    font-family: 'icomoon' !important;
                    speak: none;
                    font-style: normal;
                    font-weight: normal;
                    font-variant: normal;
                    text-transform: none;
                    line-height: 1;
                    -webkit-font-smoothing: antialiased;
                    -moz-osx-font-smoothing: grayscale;
                    
                    font-size: 24px!important;
                    color: $blue;
                    
                    display: inline-block;
                }
            }
            &-prev {
                transform: rotate(-180deg);
                left: 0px;
                margin-top: -10px;
            }
            &-next {
                right: 0px;
            }
            &-dot {
                width: 6px;
                height: 6px;
                border-radius: 50%;
                background: $blue!important;
                margin: 0 6px;
                &.active {
                    width: 12px;
                    height: 12px;
                    box-shadow: 0px 2.97398px 7.43494px rgba(197, 50, 56, 0.5);
                    background: $red!important;
                }
                &s {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    padding: 15px 0;
                    
                }
            }
        }
        &-name {
            font-style: normal;
            font-weight: bold;
            font-size: 20px;
            line-height: 23px;
            letter-spacing: 0.01em;
            
            padding-bottom: 50px;
            @media (max-width:768px) {
                display: none;
            }
        }
        @media (max-width:768px) {
            &-img {
                &:not(.owl-carousel) {
                    background: #fff;
                    margin-left: -10px;
                    margin-right: -10px;
                    width: auto;
                }
            }  
        }
        &-more {
            color: $grey;
            display: flex;
            align-items: center;
            padding-top: 26px;
            text-decoration: none;
            transition: $tr;
            @media (min-width:769px) {
                display: none;
            }
            &:hover {
                color: $red;
            }
            .icon-more {
                margin-right: 13px;
            }
        }
    }
    &__tab {
        &s {
            width: 100%;
            max-width: 400px;
            @media (max-width:768px) {
                display: none;
                &.fancybox-content {
                    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                    border-radius: 5px
                }
            }
        }
        &-head {
            justify-content: space-between;
            font-style: normal;
            font-weight: 300;
            font-size: 14px;
            line-height: 19px;
            color: #A2A8C0;
            @media (max-width:768px) {
                display: none;
            }
        }
        &-a {
            cursor: pointer;
            padding-bottom: 18px;
            border-bottom: 1px solid rgba($blue, 0);
            &.active {
                font-weight: 500;
                color: $blue;
                border-bottom-color: $blue;
                
            }
        }
        &-item {
            padding-top: 25px;
            
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 16px;
            
            display: none;
            
            color: $grey;
            @media (max-width:768px) {
                display: block!important;
                
                &:before {
                    content: attr(data-item);
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 19px;
                    
                    color: $blue;
                    margin-bottom: 15px;
                    
                    display: block;
                }
                
                &:nth-child(1) {
                    order: 2;
                }
                &:nth-child(2) {
                    order: 1;
                }
                &:nth-child(3) {
                    order: 3;
                }
            }
            &.active {
                display: flex;
            }
            &in {
                margin-bottom: 20px;
                width: 40%;
                div {
                    margin-bottom: 20px;
                }
            }
        }
        &-block {
            @media (max-width:768px) {
                display: flex;
                flex-direction: column;
            }
        }
    }
}
.advice {
    padding-top: 17px;
    
    margin-bottom: 50px;
    flex-wrap: wrap;
    .catalog {
        &__item {
            width: calc( 25% - 8px);
            &:last-child {
                    display: none;
                }
            @media (min-width:769px) {
                width: calc( 20% - 8px);
                &:last-child {
                    display: block;
                }
            }
            @media (max-width:650px) {
                width: calc( 50% - 8px);
            }
            
        }
    }
}